
// TYPES
import types from '../actions/types';

// initial values
const initialValue = {
    authInfo: null,
    loading: false
}

export default function authReducer(state = initialValue, action) {
    switch (action.type) {

            
        case types.AUTH_INFO:
            return { 
                ...state,
                authInfo: action.payload.authInfo
            };
        
        case types.LOADING:
            return {
                ...state,
                loading: action.payload
            }

        default:
            return state;
    }
}
