
// NODE MODULES
import React from 'react';
import { connect } from 'react-redux';

// APIS
import votifyServer from '../api/votifyServer';

// ACTIONS
import actions from '../actions/actions';

// CSS
import '../styles/components/EmailResetForm.css';

class EmailResetForm extends React.Component {
    
    constructor(props) {
        super(props);
        this.state = {
            newEmail: ''
        };


    }

    // RENDER FUNCTIONS
    renderAuthInfo(message) {
        if (message) {
            return (
                <div className="email-reset-form__info">
                    <p>{message}</p>
                </div>
            );
        };

        return null;
    }

    renderForm() {
        return (
            <div className="email-reset-form__form">

                <div>
                    {this.renderAuthInfo(this.props.authInfo)}
                </div>

                <div>
                    <input 
                        placeholder="New Email..."
                        type="email"
                        value={this.state.newEmail}
                        onChange={(event) => {
                            this.setState({ ...this.state, newEmail: event.target.value });
                        }}
                    />
                </div>

                <div>
                    <button
                        onClick={() => {
                            this.props.resetEmail({ 
                                newEmail: this.state.newEmail
                            });
                        }}
                    >
                        Reset Email
                    </button>
                </div>

            </div>
        );
    };

    componentDidMount() {

    }

    render() {
        return (
            <div className="email-reset-form__container">
                <div className="email-reset-form__content">
                    {this.renderForm()}
                </div>
            </div>
        );
    };
}

function mapStateToProps(state) {
    return {
        authInfo: state.auth.authInfo,
        loading: state.auth.loading
    }
}

function mapDispatchToProps(dispatch) {
    return {
        resetEmail: async ({ newEmail }) => {
            try {

                dispatch(actions.loading(true));

                if (!newEmail) {
                    dispatch(actions.authInfo({ authInfo: 'Please fill all the credentials' }));
                }

                const url = window.location.href;
                const response = await votifyServer.post(url, { newEmail });
                const { data } = response;
        
                if (data.success) {
                    dispatch(actions.authInfo({ authInfo: data.msg }));
                }

                dispatch(actions.loading(false));

            } catch (error) {
                dispatch(actions.loading(false));
                dispatch(actions.authInfo({ authInfo: 'Something went wrong, please try again'}));
            }

            dispatch(actions.loading(false));
    
        }
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(EmailResetForm);

